/** @jsx jsx */
import { css, jsx } from '@emotion/react'
import { HeadFC, Link, PageProps, graphql } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import * as React from "react"
import Layout from "../components/layout"
import { breakpoints, colors, fonts } from '../components/theme'

// demo https://flexible-gatsby.netlify.app/

const posts = css`
  display: flex;
  flex-wrap: wrap;
  padding: 20px 20px 20px 260px;
  @media (max-width: ${breakpoints[0]}px) {
    padding: 20px;
    justify-content: space-between;
  }
`

const post = css`
  margin-bottom: 1.5rem;
  display: flex;
  background-color: #FFFFFF;
  border-radius: 10px;
  box-sizing: border-box;
  align-items: stretch;
  box-shadow: 0 1px 1px 0 rgb(31 35 46 / 15%);
  overflow: hidden;
  transition-duration: .3s;
  transition-property: all;
  &:hover {
    transform: translate(0px, -2px);
    box-shadow: 0px 15px 45px -10px rgba(10, 16, 34, .2);
  }

  flex-direction: row;
  width: 100%;
  @media (max-width: ${breakpoints[1]}px) {
    width: 48%;
    margin: 0 .9% 1.5%;
    flex-direction: column;
    box-sizing: border-box;
    align-items: stretch;
  }
`

const thumbnail = css`
  background-position: 50% 50%;
  max-width: 100%;

  height: 100%;
  width: 30%;
  @media (max-width: ${breakpoints[1]}px) {
    width: 100%;
    height: 30%;
  }
`

const title = css`
  color: ${colors.secondary};
  font-family: ${fonts.secondary};
  font-weight: 300;
  font-size: 30px;
  line-height: 45px;
  margin: 0 0 10px;
`

const smallText = css`
  font-size: 12px;
  line-height: 18px;
`

const post_content = css`
  padding: 1rem;
  margin: 0;
  width: 70%;
  box-sizing: border-box;
  @media (max-width: ${breakpoints[1]}px) {
    width: 100%;
  }
`

const excerpt = css`
  font-weight: 300;
  line-height: 24px;
  margin: 0 0 10px;
`

const IndexPage: React.FC<PageProps<Queries.IndexPageQuery>> = ({ data }) => {
  return (
    <Layout>
      <div css={posts}>
      {
      data.allMdx.nodes.map( node => (
        <article css={post} key={node.id}>
          {
            node.frontmatter?.img &&
            <GatsbyImage
              // @ts-ignore
              image={getImage(node.frontmatter?.img)}
              css={thumbnail}
              alt={node.id}
            />
          }
          <div css={post_content}>
            <Link
              to={`/${node.frontmatter?.slug}`}
              css={css`
                text-decoration: none;
              `}
            >
              <h2 css={title}>{node.frontmatter?.title}</h2>
            </Link>
            <p css={excerpt}>{node.excerpt}</p>
            <span css={smallText}>{node.frontmatter?.date}</span>
          </div>
        </article>
      ))
      }
      </div>
    </Layout>
  )
}

export default IndexPage

export const query = graphql`
query IndexPage {
  allMdx(sort: {frontmatter: {date: DESC}}) {
    nodes {
      id
      frontmatter {
        title
        slug
        date(formatString: "YYYY, MMM DD")
        img {
          childImageSharp {
            gatsbyImageData
          }
        }
      }
      excerpt
    }
  }
}
`

export const Head: HeadFC = () => <title>Home Page</title>
